mplate>\n                </el-table-column>\n                <el-table-column\n                  label=\"书名|作者\"\n                  show-overflow-tooltip>\n                  <template slot-scope=\"scope\">\n                    <span>《{{scope.row.book.bookName}}》| <span>{{scope.row.book.author}}</span></span>\n                  </template>\n                </el-table-column>\n                <el-table-column\n                  label=\"出版社\"\n                  show-overflow-tooltip>\n                  <template slot-scope=\"scope\">\n                    <span>{{scope.row.book.publisher}}</span>\n                  </template>\n                </el-table-column>\n                <el-table-column\n                  label=\"单价\"\n                  width=\"120\">\n                  <template slot-scope=\"scope\">{{scope.row.book.price*scope.row.book.discount}}</template>\n                </el-table-column>\n                <el-table-column\n                  label=\"数量\"\n                  show-overflow-tooltip>\n                  <template slot-scope=\"scope\">\n                    {{scope.row.buyNum}}\n                  </template>\n                </el-table-column>\n                <el-table-column\n                  label=\"小计\"\n                  show-overflow-tooltip>\n                  <template slot-scope=\"scope\">{{scope.row.book.price*scope.row.book.discount*scope.row.buyNum }}</template>\n                </el-table-column>\n              </el-table>\n\n              <div class=\"cart-footer\" v-show=\"orderitem.length\">\n                <div class=\"cart-footer-desc\">\n                  共计 <span>{{countAll}}</span> 件商品\n                </div>\n                <div class=\"cart-footer-desc\">\n                  应付总额 <span>¥ {{costAll}}</span>\n                </div>\n                <div style=\"margin-top: 20px\">\n                  <el-button class=\"buy-btn\" @click=\"submitOrder\">提价订单</el-button>\n                </div>\n              </div>\n            </div>\n          </el-main>\n        <pagefooter></pagefooter>\n      </el-container>\n    </div>\n</template>\n\n<script>\n  import {addorder} from '../axios/api'\n  import pageheader from \"../components/pageheader\";\n  import pagefooter from \"../components/pagefooter\";\n    export default {\n        name: \"userorder\",\n      components: {\n        pageheader,\n        pagefooter\n      },\n        data:function(){\n          return{\n            orderitem:[],\n            addrForm:\"\",\n            uporder:{\n              carIds:[],\n              userId:0,\n              amount:0\n            }\n          }\n        },\n      computed:{\n         countAll(){\n           var count=0;\n             this.orderitem.forEach(data=>{\n               count+=data.buyNum\n           })\n           return count;\n         },\n        costAll(){\n           var cost=0;\n          this.orderitem.forEach(data=>{\n            cost+=  data.book.price*data.book.discount*data.buyNum\n          })\n          return cost;\n        },\n        getCarIds(){\n           var carIds=[];\n          this.orderitem.forEach(data=>{\n            carIds.push(data.carId)\n          })\n          return carIds;\n        }\n      },\n        mounted() {\n          JSON.stringify(this.$route.params.orderlist)\n          let orderlist=[];\n          orderlist=this.$route.params.orderlist\n          orderlist.forEach(data=>{\n            data.book.imgurl=data.book.imgurl.split(\",\");\n          })\n          this.orderitem=orderlist;\n        },\n      methods:{\n        submitOrder(){\n          this.uporder.carIds=this.getCarIds\n          this.uporder.userId=this.$store.state.user.userId\n           this.uporder.amount=this.costAll\n          console.log(this.uporder)\n          var params=this.uporder;\n          addorder(params).then(res=>{\n            console.log(res.data)\n            this.$message({\n              message:'下单成功'\n            })\n              this.$store.commit('setUserInfoTag','myorders');\n              this.$router.push('/myorders')\n          })\n        }\n      }\n    }\n</script>\n\n<style scoped>\n.address-form{\n  width: 1226px;\n  padding-bottom: 50px;\n}\n  .address-form .el-form-item >>>.el-input{\n    width: 300px;\n  }\n</style>\n"],"sourceRoot":""}]);

